import React from 'react';
import AppCard from '../../../@crema/core/AppCard';
import { Box } from '@material-ui/core';
import { Fonts } from '../../constants/AppEnums';
// import {useIntl} from 'react-intl';
import AppAnimate from '../../../@crema/core/AppAnimate';
import { User } from 'types/models/User';
import { countries } from 'country-data';
import Moment from 'react-moment';

interface UserDataProps {
  userData: User;
}

const UserSummary: React.FC<UserDataProps> = ({ userData }) => {
  // const {messages} = useIntl();
  return (
    <AppAnimate animation='transition.slideUpIn' delay={200}>
      <AppCard
        title={
          <Box fontSize={16} fontWeight={Fonts.BOLD}>
            User Details
          </Box>
        }
        rootStyle={{ height: '100%' }}
      >
        <Box
          display='flex'
          justifyContent='space-between'
          fontSize={14}
          fontWeight={Fonts.MEDIUM}
          mt={2}
          mb={4}>
          <Box color='text.secondary'>User ID: </Box>
          <Box>{userData.user_no}</Box>
        </Box>
        <Box
          display='flex'
          justifyContent='space-between'
          fontSize={14}
          fontWeight={Fonts.MEDIUM}
          mb={4}>
          <Box color='text.secondary'>User IP: </Box>
          <Box>{userData.ip}</Box>
        </Box>
        <Box
          display='flex'
          justifyContent='space-between'
          fontSize={14}
          fontWeight={Fonts.MEDIUM}
          mb={4}>
          <Box color='text.secondary'>Country: </Box>
          <Box>{countries[userData.country || ''].name}</Box>
        </Box>
        <Box
          display='flex'
          justifyContent='space-between'
          fontSize={14}
          fontWeight={Fonts.MEDIUM}
          mb={4}>
          <Box color='text.secondary'>Time: </Box>
          <Box>
            <Moment fromNow>{userData.createdAt}</Moment>
          </Box>
        </Box>
      </AppCard>
    </AppAnimate>
  );
};

export default UserSummary;
