import React, { useEffect } from 'react';
import { useParams } from 'react-router';
import { useDispatch, useSelector } from 'react-redux';
import { onGetUserData } from '../../redux/actions';
import AppAnimate from '../../@crema/core/AppAnimate';
import { AppState } from '../../redux/store';
import UserView from 'shared/components/UserView';

const UserControlPanel = () => {
    const dispatch = useDispatch();

    const { id } = useParams<{ id: string }>();

    useEffect(() => {
        dispatch(onGetUserData(id));
    }, [dispatch, id]);

    const { user, clientDomain } = useSelector<AppState, AppState['users']>(
        ({ users }) => users,
    );

    return (
        <>
            {user === null ? null :
                <AppAnimate animation='transition.slideUpIn' delay={200}>
                    <UserView userData={user} clientDomain={clientDomain} />
                </AppAnimate>
            }
        </>
    );
};

export default UserControlPanel;
